/* 
** Importing globals
** - tailwind
** - index
 */
@tailwind base;
@tailwind components;
@tailwind utilities;
* {
  font-family: "Work Sans", sans-serif;
}

/* 
** Importing layouts
** - master
** - navigation
** - footer
 */
.page__wrapper {
  @apply w-full;
}
.page__wrapper .nav__wrapper {
  @apply relative z-50;
}
.page__wrapper .footer__wrapper {
  @apply bg-agray border-t;
}

.nav {
  @apply container mx-auto px-3;
  @apply w-full h-20 grid grid-cols-3 items-center relative;
}
.nav .nav__section {
  @apply h-9;
}
.nav .nav__section--logo {
  @apply h-full flex flex-col justify-center;
}
.nav .nav__section--logo .logo__link {
  @apply w-full h-9;
}
.nav .nav__section--logo .logo__link .logo__img {
  @apply w-full h-full object-contain object-left;
}
.nav .nav__section--menu {
  @apply w-full max-w-80 h-full justify-self-center flex flex-col justify-center;
}
.nav .nav__section--menu .menu__lists--desktop {
  @apply flex justify-between items-center;
}
.nav .nav__section--menu .menu__lists--desktop .menu__link {
  @apply text-lg font-normal text-secondary capitalize;
}
.nav .nav__section--menu .menu__lists--desktop .menu__link.menu__link--active {
  @apply font-medium;
}
.nav .nav__section--menu .menu__lists--desktop .menu__link:hover {
  @apply text-secondary-alt font-medium;
}
@media (max-width: 768px) {
  .nav .nav__section--menu .menu__lists--desktop {
    @apply hidden;
  }
}
.nav .nav__section--menu .menu__lists--mobile {
  @apply w-full py-5 flex flex-col items-center bg-white absolute top-20 left-0 rounded-md border translate-y-20 opacity-0 invisible duration-100;
}
.nav .nav__section--menu .menu__lists--mobile .menu__list .menu__link {
  @apply w-fit flex justify-center items-center p-3 text-lg font-normal text-secondary capitalize;
}
.nav .nav__section--menu .menu__lists--mobile .menu__list .menu__link:hover {
  @apply font-medium;
}
.nav .nav__section--menu .menu__lists--mobile .menu__list.menu__list--button {
  @apply w-fit h-9 mt-4 px-4 flex justify-center items-center rounded-md bg-agray text-agray-alt text-lg font-normal;
}
.nav .nav__section--menu .menu__lists--mobile .menu__list.menu__list--button:hover {
  @apply font-medium text-secondary;
}
.nav .nav__section--menu .menu__lists--mobile.menu__lists--mobile--show {
  @apply translate-y-0 opacity-100 visible;
}
.nav .nav__section--menu .menu__btn--open {
  @apply w-9 h-9 flex items-center justify-center self-end rounded-md bg-agray text-agray-alt text-lg font-normal;
}
.nav .nav__section--menu .menu__btn--open:hover {
  @apply font-medium text-secondary;
}
.nav .nav__section--menu .menu__btn--close {
  @apply w-9 h-9 flex items-center justify-center self-end rounded-md bg-agray text-agray-alt text-lg font-normal;
}
.nav .nav__section--menu .menu__btn--close:hover {
  @apply font-medium text-secondary;
}
.nav .nav__section--menu .menu__btn--hidden {
  @apply hidden;
}
@screen md {
  .nav .nav__section--menu .menu__btn--open,
  .nav .nav__section--menu .menu__btn--close,
  .nav .nav__section--menu .menu__lists--mobile {
    @apply hidden;
  }
}
.nav .nav__section--button {
  @apply h-full flex flex-col justify-center items-end;
}
.nav .nav__section--button .button__login {
  @apply w-fit flex items-center justify-center py-1.5 px-4 rounded-md bg-agray text-agray-alt text-lg font-normal;
}
.nav .nav__section--button .button__login:hover {
  @apply text-secondary font-medium;
}
@media (max-width: 768px) {
  .nav .nav__section--button {
    @apply hidden;
  }
}
@media (max-width: 768px) {
  .nav {
    @apply grid-cols-2;
  }
}

.footer {
  @apply container mx-auto px-3;
  @apply w-full h-max flex flex-col items-center relative;
}
.footer .footer__upper {
  @apply w-full py-9 grid grid-cols-6 items-center gap-8;
}
.footer .footer__upper .footer__section {
  @apply w-full h-full flex flex-col justify-start items-start gap-4;
}
.footer .footer__upper .footer__section .section__heading {
  @apply text-lg text-secondary-alt font-semibold first-letter:capitalize;
}
.footer .footer__upper .footer__section .section__list {
  @apply flex flex-col justify-start items-start gap-2;
}
.footer .footer__upper .footer__section--about {
  @apply max-w-[450px] col-span-2;
}
.footer .footer__upper .footer__section--about .list__content {
  @apply text-base text-secondary;
}
.footer .footer__upper .footer__section--lists .section__list {
  @apply text-base text-secondary-alt;
}
.footer .footer__upper .footer__section--lists .section__list:hover {
  @apply text-secondary-alt underline;
}
.footer .footer__upper .footer__section--news {
  @apply w-full max-w-[400px] h-64 col-span-2 justify-self-center bg-white rounded-xl;
}
.footer .footer__upper .footer__section--news .news__form {
  @apply w-full h-full p-9 flex flex-col;
}
.footer .footer__upper .footer__section--news .news__form .news__heading {
  @apply w-full mb-6 text-center;
}
.footer .footer__upper .footer__section--news .news__form .news__heading h3 {
  @apply mb-1 text-xl text-secondary-alt font-semibold first-letter:capitalize;
}
.footer .footer__upper .footer__section--news .news__form .news__heading p {
  @apply text-base text-agray-alt font-normal;
}
.footer .footer__upper .footer__section--news .news__form .news__search {
  @apply w-full h-14 py-2 px-4 mb-2 flex justify-between items-center border rounded-md;
}
.footer .footer__upper .footer__section--news .news__form .news__search input {
  @apply w-11/12 mr-1 h-full outline-none;
}
.footer .footer__upper .footer__section--news .news__form .news__search i {
  @apply w-1/12 h-full flex justify-center items-center;
}
.footer .footer__upper .footer__section--news .news__form .news__button {
  @apply w-full h-14 bg-primary text-lg text-white font-normal text-center rounded-md;
}
.footer .footer__upper .footer__section--news .news__form .news__button:hover {
  @apply bg-primary-alt;
}
@media (max-width: 768px) {
  .footer .footer__upper .footer__section--news {
    @apply max-w-full !important;
  }
}
@media (max-width: 768px) {
  .footer .footer__upper .footer__section {
    @apply col-span-1 !important;
  }
}
@media (max-width: 1024px) {
  .footer .footer__upper {
    @apply grid-cols-2;
  }
}
@media (max-width: 768px) {
  .footer .footer__upper {
    @apply grid-cols-1;
  }
}
.footer .footer__lower {
  @apply w-full py-9 flex justify-between items-center gap-2 flex-wrap border-t;
}
.footer .footer__lower .footer__section {
  @apply w-fit h-full flex justify-start items-center;
}
.footer .footer__lower .footer__section--left {
  @apply gap-3;
}
@media (max-width: 1024px) {
  .footer .footer__lower .footer__section--left {
    @apply flex-col;
  }
  .footer .footer__lower .footer__section--left .section__copyrights {
    @apply flex flex-col items-center;
  }
}
.footer .footer__lower .footer__section--right .section__lists {
  @apply w-full flex justify-start items-center gap-3;
}
.footer .footer__lower .footer__section--right .section__lists .section__list {
  @apply flex flex-col justify-start items-start gap-2 text-base text-secondary-alt;
}
.footer .footer__lower .footer__section--right .section__lists .section__list:hover {
  @apply text-secondary-alt underline;
}
.footer .footer__lower .footer__section--right .section__lists :not(li:last-child) .section__list {
  @apply border-r-2 pr-3;
}
@media (max-width: 1024px) {
  .footer .footer__lower .footer__section--right .section__lists {
    @apply justify-center flex-wrap;
  }
}
@media (max-width: 1024px) {
  .footer .footer__lower .footer__section {
    @apply w-full;
  }
}

/* 
** Importing components
** - header
** - card
** - post
 */
.header__wrapper {
  @apply w-full;
}
.header__wrapper .header__container {
  @apply container mx-auto px-3;
  @apply w-full h-max my-14 pb-[52px] relative;
}
.header__wrapper .header__container .header__cover {
  @apply w-full h-[500px] object-cover rounded-xl;
}
.header__wrapper .header__container .header__cover--card {
  @apply h-[600px];
}
.header__wrapper .header__container .header__cover--inner {
  @apply h-[450px];
}
@media (max-width: 768px) {
  .header__wrapper .header__container .header__cover {
    @apply h-[350px];
  }
  .header__wrapper .header__container .header__cover.header__cover--card {
    @apply h-[450px];
  }
}
.header__wrapper .header__container .header__card {
  @apply w-[600px] h-max p-10 flex flex-col bg-white border rounded-xl gap-8 shadow-[0px_0px_5px_-2px_#4a5568] absolute bottom-0 left-[52px];
}
.header__wrapper .header__container .header__card .card__category {
  @apply w-fit py-1.5 px-3 text-sm first-letter:capitalize text-white bg-primary rounded-lg;
}
.header__wrapper .header__container .header__card .card__category:hover {
  @apply underline;
}
.header__wrapper .header__container .header__card .card__post {
  @apply w-full text-4xl text-secondary-alt font-semibold first-letter:capitalize;
}
.header__wrapper .header__container .header__card .card__post:hover {
  @apply underline;
}
@media (max-width: 768px) {
  .header__wrapper .header__container .header__card .card__post {
    @apply text-2xl;
  }
}
.header__wrapper .header__container .header__card .card__author {
  @apply w-full flex items-center justify-start gap-6;
}
.header__wrapper .header__container .header__card .card__author .card__author__img {
  @apply w-9 h-9 object-cover rounded-full;
}
.header__wrapper .header__container .header__card .card__author .card__author__link {
  @apply text-sm font-medium first-letter:capitalize text-agray-alt;
}
.header__wrapper .header__container .header__card .card__author .card__author__link:hover {
  @apply underline;
}
.header__wrapper .header__container .header__card .card__author .card__author__postDate {
  @apply text-sm font-normal first-letter:capitalize text-agray-alt;
}
@media (max-width: 768px) {
  .header__wrapper .header__container .header__card {
    @apply w-[calc(100%_-_48px)] left-2/4 -translate-x-2/4;
  }
}
.header__wrapper .header__container .header__card--inner {
  @apply w-[calc(70%_-_20px)] bottom-0 left-3 translate-x-0 bg-transparent border-none shadow-none;
}
.header__wrapper .header__container .header__card--inner .card__post {
  @apply text-white;
}
.header__wrapper .header__container .header__card--inner .card__author .card__author__link {
  @apply text-white;
}
.header__wrapper .header__container .header__card--inner .card__author .card__author__postDate {
  @apply text-white;
}
@media (max-width: 768px) {
  .header__wrapper .header__container .header__card--inner {
    @apply w-full;
  }
}
@media (max-width: 768px) {
  .header__wrapper .header__container {
    @apply pb-[104px];
  }
}
.header__wrapper .header__container--breadcrumbs {
  @apply mb-0 pb-0 flex flex-col justify-center items-center gap-2;
}
.header__wrapper .header__container--breadcrumbs .breadcrumbs__heading {
  @apply text-3xl text-secondary-alt font-semibold;
}
.header__wrapper .header__container--breadcrumbs .breadcrumbs__links {
  @apply flex justify-center items-center gap-3;
}
.header__wrapper .header__container--breadcrumbs .breadcrumbs__links .breadcrumbs__link {
  @apply text-base text-agray-alt font-normal capitalize;
}
.header__wrapper .header__container--breadcrumbs .breadcrumbs__links .breadcrumbs__link:hover {
  @apply underline;
}
.header__wrapper .header__container--breadcrumbs .breadcrumbs__links .breadcrumbs__link--active {
  @apply text-secondary-alt;
}
.header__wrapper .header__container--breadcrumbs .breadcrumbs__links .breadcrumbs__link--active:hover {
  @apply no-underline;
}
.header__wrapper .header__container--breadcrumbs .breadcrumbs__links :not(li:last-child) .breadcrumbs__link {
  @apply border-r-2 pr-3;
}
.header__wrapper .header__container--inner {
  @apply pb-0;
}
.header__wrapper .header__container--author {
  @apply pb-0;
}
.header__wrapper .header__container--author .header__card--author {
  @apply w-full h-[450px] p-9 flex flex-col justify-center items-center gap-6 bg-agray rounded-xl;
}
.header__wrapper .header__container--author .header__card--author .card__upper {
  @apply w-full flex justify-center items-center gap-4;
}
.header__wrapper .header__container--author .header__card--author .card__upper .card__author__img {
  @apply w-16 h-16 object-cover rounded-full;
}
.header__wrapper .header__container--author .header__card--author .card__upper .card__author__title {
  @apply text-xl text-secondary-alt font-medium;
}
.header__wrapper .header__container--author .header__card--author .card__upper .card__author__job {
  @apply text-sm text-agray-alt font-normal;
}
.header__wrapper .header__container--author .header__card--author .card__author__desc {
  @apply w-full max-w-screen-sm text-lg text-secondary font-medium text-center;
}
.header__wrapper .header__container--author .header__card--author .card__author__socials {
  @apply w-full max-w-screen-sm flex justify-center items-center gap-3;
}
.header__wrapper .header__container--author .header__card--author .card__author__socials .social__link {
  @apply w-9 h-9 flex justify-center items-center bg-secondary text-white rounded-md;
}
.header__wrapper .header__container--author .header__card--author .card__author__socials .social__link:hover {
  @apply bg-secondary-alt;
}
@media (max-width: 768px) {
  .header__wrapper .header__container--author .header__card--author {
    @apply h-max;
  }
}
.header__wrapper .header__container--post-single {
  @apply max-w-screen-md mb-6 pb-0;
}
.header__wrapper .header__container--post-single .header__card {
  @apply w-full mb-6 p-0 bg-white border-none shadow-none relative bottom-0 left-0;
}
.header__wrapper .header__container--post-single .header__card .card__post {
  @apply w-full text-4xl text-secondary-alt font-semibold first-letter:capitalize;
}
.header__wrapper .header__container--post-single .header__card .card__post:hover {
  @apply underline;
}
@media (max-width: 768px) {
  .header__wrapper .header__container--post-single .header__card .card__post {
    @apply text-2xl;
  }
}
@media (max-width: 768px) {
  .header__wrapper .header__container--post-single .header__card {
    @apply w-full left-0 translate-x-0;
  }
}
@media (max-width: 768px) {
  .header__wrapper .header__container--post-single .header__cover {
    @apply h-[200px] !important;
  }
}

.group__wrapper {
  @apply w-full;
}
.group__wrapper .group__container {
  @apply container mx-auto px-3;
  @apply w-full h-max my-14 pb-[52px] relative;
}
.group__wrapper .group__container .group__heading {
  @apply w-full mb-9;
}
.group__wrapper .group__container .group__heading .heading__title {
  @apply text-2xl text-secondary-alt font-bold;
}
.group__wrapper .group__container .group__posts {
  @apply w-full mb-9 grid grid-cols-3 gap-4;
}
.group__wrapper .group__container .group__posts .post__card {
  @apply w-full h-max p-4 flex flex-col bg-white border rounded-xl gap-4 shadow-[0px_0px_1px_-2px_#4a5568];
}
.group__wrapper .group__container .group__posts .post__card .card__cover {
  @apply w-full h-72 rounded-md overflow-hidden;
}
.group__wrapper .group__container .group__posts .post__card .card__cover .card__cover--img {
  @apply w-full h-full object-cover duration-200;
}
.group__wrapper .group__container .group__posts .post__card .card__cover .card__cover--img:hover {
  @apply scale-110;
}
.group__wrapper .group__container .group__posts .post__card .card__category {
  @apply w-fit py-1.5 px-3 text-sm first-letter:capitalize text-primary bg-primary-light rounded-lg;
}
.group__wrapper .group__container .group__posts .post__card .card__category:hover {
  @apply underline;
}
.group__wrapper .group__container .group__posts .post__card .card__post {
  @apply w-full text-2xl text-secondary-alt font-semibold first-letter:capitalize;
}
.group__wrapper .group__container .group__posts .post__card .card__post:hover {
  @apply underline;
}
@media (max-width: 768px) {
  .group__wrapper .group__container .group__posts .post__card .card__post {
    @apply text-2xl;
  }
}
.group__wrapper .group__container .group__posts .post__card .card__author {
  @apply w-full flex items-center justify-start gap-6;
}
.group__wrapper .group__container .group__posts .post__card .card__author .card__author__img {
  @apply w-9 h-9 object-cover rounded-full;
}
.group__wrapper .group__container .group__posts .post__card .card__author .card__author__link {
  @apply text-sm font-medium first-letter:capitalize text-agray-alt;
}
.group__wrapper .group__container .group__posts .post__card .card__author .card__author__link:hover {
  @apply underline;
}
.group__wrapper .group__container .group__posts .post__card .card__author .card__author__postDate {
  @apply text-sm font-normal first-letter:capitalize text-agray-alt;
}
@media (max-width: 1024px) {
  .group__wrapper .group__container .group__posts {
    @apply grid-cols-2;
  }
}
@media (max-width: 768px) {
  .group__wrapper .group__container .group__posts {
    @apply grid-cols-1;
  }
}
.group__wrapper .group__container .group__footer {
  @apply w-full text-center;
}
.group__wrapper .group__container .group__footer .footer__button {
  @apply w-fit block mx-auto py-2 px-5 text-lg text-agray-alt font-medium bg-white border rounded-md;
}
.group__wrapper .group__container .group__footer .footer__button:hover {
  @apply text-secondary;
}
@media (max-width: 768px) {
  .group__wrapper .group__container {
    @apply pb-[104px];
  }
}

.article__wrapper {
  @apply container mx-auto px-3;
  @apply max-w-screen-md mb-14;
}
.article__wrapper p {
  @apply mb-6 text-base text-secondary-alt font-normal;
}
.article__wrapper h4 {
  @apply mb-1 text-2xl text-secondary-alt font-semibold;
}
.article__wrapper h4 strong {
  @apply font-semibold;
}
.article__wrapper blockquote {
  @apply bg-agray mb-6 py-4 px-6 text-xl font-medium italic border-l-4 rounded-xl;
}
.article__wrapper blockquote p {
  @apply mb-0;
}
.article__wrapper figure {
  @apply max-h-[415px] mb-6 rounded-xl overflow-hidden;
}